<script>
export default {
	name: 'Button',
	props: {
		isDisabled: {
			type: Boolean,
			default: false
		},
		cb: {
			type: Function,
			default: () => {}
		}
	},
	methods:{
		handleClick(e){
			if(this.isDisabled){
				e.preventDefault()
			}
		}
	}
};
</script>

<template>
	<button :disabled="isDisabled" @click="this.cb"><span @click="handleClick" ><slot/></span></button>
</template>

<style lang="scss" scoped>
@import '../scss/colors';

button{
	box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2), 0px 2px 2px rgba(0, 0, 0, 0.12), 0px 0px 2px rgba(0, 0, 0, 0.14);
	border-radius: 24px;
	padding: 8px 24px;
	font-weight: bold;
	font-size: 16px;
	line-height: 24px;
	cursor:pointer;
	outline: none;
	transition:.25s;
	background: $btfs-blue-500-main;
	color: $gray-050;
	position:relative;
	border:none;

	> span{
		position:relative;
	}

	&:hover{
		background: $btfs-blue-400;
	}

	&:before {
		content: "";
		position: absolute;
		width: 0;
		height: 0;
		top: 50%;
		left: 50%;
		transform-style: flat;
		transform: translate3d(-50%,-50%,0);
		background: $btfs-blue-600;
		border-radius: 24px;
		transition: width .2s ease, height .2s ease;
	}

	&:active {
		background: $btfs-blue-500-main;

		&:before {
			width: 100%;
			height: 100%;
		}
	}

	&:disabled{
		background: $gray-300;
		color: $gray-500;
		cursor:default;

		&:before {
			width:0;
			height:0;
		}
	}
}


</style>