<script>
import Nav from '../components/Nav.vue'
import ClipboardJS from 'clipboard'
let clipboard

export default {
	name: 'Dashboard',
	props: {
		version: {
			type: String,
			required: true
		},
		id: {
			type: String,
			required: true
		}
	},
	components: {
		Nav,
	},
	methods: {
		handleNavClick(navItem) {
			this.selectedNavItem = navItem
		},
		copyClick() {
			
		}
	},
	data() {
		return {
			navList: [
				'host',
				'wallet'
			],
			selectedNavItem: 'host',
			showCopyTooltip: false
		}
	},
	computed: {
	},
	mounted() {
		clipboard = new ClipboardJS('.copy-icon')
		clipboard.on('success', (e) => {
				this.showCopyTooltip = true;
				setTimeout(()=>{this.showCopyTooltip=false},1000)
				e.clearSelection();
		});
	},
	beforeDestroy() {
		clipboard.destroy()
	}
}
</script>

<template>
	<div class="settings">
		<Nav/>
		<div class="main">
			<p class="title">Settings</p>
			<div class="settings-nav">
				<div
					v-for="navItem in navList" 
					:key="navItem"
					class="settings-nav-item"
					:class="{'settings-nav-item-selected': navItem === selectedNavItem}"
					@click="()=> {handleNavClick(navItem)}"
				>
					{{ navItem }}
				</div>
			</div>
			<div 
				v-if="selectedNavItem === 'host'"
				class="settings-host"
			>
				<p class="subtitle">
					current version
				</p>
				<p class="content">
					{{ version }}
				</p>
				<p class="subtitle">
					node id
				</p>
				<div class="content node-id">
					<p ref="nodeId" id="node-id">
						{{ id }}
					</p>
					<img 
						src="../assets/svg/copy.svg" 
						class="copy-icon" 
						@click="copyClick" 
						data-clipboard-target="#node-id"
						v-tooltip.bottom="{ 
							content: 'copied!', 
							classes: ['tooltip'], 
							show: showCopyTooltip,
							trigger: 'manual'
						}"
					/>
				</div>
			</div>
			<div
				v-if="selectedNavItem === 'wallet'"
				class="settings-wallet"
			>
				BTFS Wallet is still under development. If you would like to deposit or withdraw BTT, please refer to this <a href="https://docs.btfs.io/docs/get-your-current-btt-balances" target="_blank" rel="noreferrer noopener">guide</a>.
			</div>
		</div>
	</div>
</template>

