import Vue from 'vue';
import routes from './routes';
const { request } = routes;

const state = {
    value: {},
};
const mutations = {
    setConfig(s, stats) {
        Vue.set(s, 'value', stats);
    },

};
const getters = {
    config(s) {
        return s.value;
    }
};
const actions = {
    getConfig(context) {
        return request({
            endpoint: 'v1/config',
            method: 'GET',
            params: {
                arg:'UI.Host.Initialized'
            }
        }).then((stats) => {
            // console.log('getConfig', JSON.parse(stats))
            context.commit('setConfig', JSON.parse(stats));
        });
    },
    setConfig(context) {
        return request({
            endpoint: 'v1/config',
            method: 'POST',
            params: {
                arg:'UI.Host.Initialized&arg=true'
            }
        }).then(() => {
            return context.dispatch('getConfig')
        })
    }

};

export default {
    state,
    getters,
    mutations,
    actions,
};