<script>
import  '@bittorrent/bt-ui-modules';
import colors from "../scss/_colors.scss"
import Button from '../components/Button.vue'

export default {
  name: 'LandingPage',
  props: {
    hasEnoughSpace: {
      type: Boolean,
      required: true,
      default:true
    }
  },
  components: {
    Button
  },
  data() {
    return {
      btfsBlue500 : colors.btfsBlue500,
      showPopup : false
    }
  },
  methods: {
    handleClose() {
      this.showPopup = !this.showPopup
    },
    handleNext(e) {
      if(!this.hasEnoughSpace){
        e.preventDefault();
        this.showPopup = true
      }
    }
  }

}
</script>

<template>
  <div class="landing-page">
      <img src="../assets/svg/BTFS-logo.svg" class="logo"/>
      <div class="container">
        <p class="text-lg">HOST</p>
        <p class="text-md">Use your computer’s local storage space to connect to the BTFS network and earn BTT.</p>
        <div class="button-container">
          <router-link to="/setup/2">
            <Button :cb="handleNext">Setup Host</Button>
          </router-link>
        </div>
      </div>
<!--       <bt-toggle
            :backgroundColor="btfsBlue500"
            height="16px"></bt-toggle> -->
      <div class="popup-bg" v-if="showPopup">
        <div class="popup-container">
          <div class="close-container" @click="handleClose">
            <img src="../assets/svg/close.svg"/>
          </div>
          <p class="header">
            Not enough storage space
          </p>
          <p class="description">
            32 GB is the minimum available space needed to continue setting up your host. Please use another computer with at least 32 GB of availabe space to continue. 
          </p>
        </div>
      </div>
  </div>


</template>


