<script>
import  '@bittorrent/bt-ui-modules';
import Button from '../components/Button.vue'
import SetStorageSize from '../components/SetStorageSize.vue'

export default {
  name: 'HostSetup2',
  props: {
    storageSpace: {
      type:Object,
      default:function() {
        return {
          minimum: {
            type: Number,
            required: false,
            default:0
          },
          used: {
            type:Number,
            required:true,
            default:0
          },
          cap: {
            type:Number,
            required:true,
            default:0
          },
          total: {
            type:Number,
            required:true,
            default:1000
          }
        }
      }
    },
    storageAnnounce: {
      type: Function,
      default: () => {}
    }
  },
  components: {
    Button,
    SetStorageSize,
  },
  data(){
    return {
      selectedStorageValue: this.storageSpace.cap
    }
  },
  computed: {
    isDisabled() {
      return this.selectedStorageValue < this.storageSpace.minimum
    }
  },
  methods: {
    handleCommitClick() {
      this.storageAnnounce(this.selectedStorageValue)
    }
  },
  watch:{
    storageSpace : function(){
      this.selectedStorageValue = this.storageSpace.cap
    }
  },
  created() {
    this.storageAnnounce(32)
  }
}
</script>

<template>
  <div class="host-setup">
    <div class="main">
      <p class="header">Host storage size</p>
      <p class="description">
        32 GB is the minimum host storage commitment size. Try moving the slider below to see how much more you can earn by increasing your commitment size.
      </p>
      <div class="storage-size-container">
        <SetStorageSize 
          :storageSpace="storageSpace" 
          :isDisabled="false" 
          @slideChange="selectedStorageValue = $event"/>
      </div>
      <div class="button-container">
        <router-link to="/setup/3" >
          <Button :isDisabled="isDisabled" :cb="handleCommitClick">Commit Storage</Button>
        </router-link>
      </div>
    </div>
  </div>
</template>

