import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import LandingPage from './views/LandingPage.vue'
import HostSetup2 from './views/HostSetup2.vue'
import HostSetup3 from './views/HostSetup3.vue'
import Dashboard from './views/Dashboard.vue'
import Settings from './views/Settings.vue'
import btfsStore from './store';
import Vuex from 'vuex'
import VTooltip from 'v-tooltip'
import './scss/_tooltip.scss';

Vue.config.productionTip = false

Vue.use(VueRouter)
Vue.use(Vuex)
Vue.use(VTooltip)

const routes = [
  { name:"LandingPage", path: '/setup', component: LandingPage },
  { name:"HostSetup2", path: '/setup/2', component: HostSetup2 },
  { name:"HostSetup3", path: '/setup/3', component: HostSetup3 },
  { name:"Dashboard", path: '/', component: Dashboard },
  { name:"Settings", path: '/settings', component: Settings },
]

const router = new VueRouter({
  routes // short for `routes: routes`
})
let port = (process.env.NODE_ENV === "development") ? 5001 : location.port
const store = new Vuex.Store({ strict: false });
btfsStore.install(store, { port: port });

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
